import React from "react"
import styled from "styled-components"
import LinkedinIcon from "../images/icons/linkedin_purple.svg"
import FacebookIcon from "../images/icons/facebook_purple.svg"
import TwitterIcon from "../images/icons/twitter_purple.svg"
import PropTypes from "prop-types"
import {
  FacebookShareButton,
  LinkedinShareButton,
  TwitterShareButton,
} from "react-share"

const SocialContainer = styled.div`
  display: flex;
  width: auto;

  & div:focus {
    outline: none;
  }
`

const SocialIcon = styled.img`
  height: 1.5rem;
  width: 1.5rem;
  margin-right: 0.5rem;
  margin-bottom: 0;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  &:hover {
    transform: scale(1.05);
  }
`

const Social = ({ socialConfig }) => {
  return (
    <SocialContainer>
      <LinkedinShareButton
        url={socialConfig.url + "/" + socialConfig.slug}
        title={socialConfig.title}
      >
        <SocialIcon src={LinkedinIcon} />
      </LinkedinShareButton>
      <FacebookShareButton
        url={socialConfig.url + "/" + socialConfig.slug}
        quote={socialConfig.title}
      >
        <SocialIcon src={FacebookIcon} />
      </FacebookShareButton>
      <TwitterShareButton
        url={socialConfig.url + "/" + socialConfig.slug}
        title={socialConfig.title}
        via={socialConfig.twitterHandle.split("@").join("")}
      >
        <SocialIcon src={TwitterIcon} />
      </TwitterShareButton>
    </SocialContainer>
  )
}

Social.propTypes = {
  socialConfig: PropTypes.object,
}

export default Social
