import React, { useState, useContext, useEffect } from "react"
import { graphql, Link } from "gatsby"
import styled from "styled-components"
import "gitalk/dist/gitalk.css"
import Gitalk from "gitalk"
import md5 from "md5-hash"
import { formatDate } from "../components/LatestArticles"
import { MenuState } from "../components/layout"
import Social from "../components/Social"

const PostContainer = styled.div`
  grid-column: 1 / span all;
  width: 100%;
  &:before {
    content: "";
    width: 100%;
    height: 50vh;
    background: url(${props => (props.bgImage ? props.bgImage : "")});
    background-cover: cover;
    display: block;
    background-position: center;
  }
`

export const PostMainContent = styled.div`
  @media (max-width: 761px) {
    width: ${props => (props.toggle ? "70%" : "90%")};
    left: ${props => (props.toggle ? "60%" : "50%")};
  }
  @media (min-width: 762px) {
    width: 70%;
    left: 60%;
  }

  @media (min-width: 1000px) {
    width: 60%;
    left: ${props => (props.toggle ? "55%" : "50%")};
  }

  margin-bottom: 2rem;
  position: absolute;
  top: 10%;
  transform: translate(-50%);
  background-color: ${props => props.bgColor};
  padding: 0.5rem 2rem;
  border-radius: 0.3rem;
  box-shadow: 0px 0px 3px 1px rgba(20, 20, 20, 0.1);
  transition: all 0.2s;
  padding-bottom: 4rem;

  & h1 {
    margin: 0.5rem 0 1.2rem;
  }
`

const PostHeader = styled.div`
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 0.3rem 0;
  & p {
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 0.9rem;
  }
`

const BackButton = styled.button`
  position: fixed;
  border: 1px solid #450ca0;
  border-radius: 0.3rem;
  font-size: 1rem;
  @media (max-width: 562px) {
    width: 20%;
  }
  @media (min-width: 762px) {
    width: 15%;
  }

  @media (min-width: 992px) {
    width: 10%;
  }
  bottom: 6%;
  right: 3%;
  color: #fff;
  padding: 0.3rem;

  background-color: #450ca0;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  &:hover {
    background-color: #450ca0;
    color: #fff;
  }
  &:focus {
    outline: none;
  }
  &:active {
    transform: scale(1.05);
  }
`

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  position: absolute;
  left: 0;
`

const PostTemplate = currentPostQuery => {
  const {
    wordpressPost: {
      featured_media,
      title,
      slug,
      acf,
      content,
      date,
      categories,
    },
    site: {
      siteMetadata: { url, twitterHandle },
    },
  } = currentPostQuery.data
  const { menu, setArticle, setProject } = useContext(MenuState)
  const gitalk = new Gitalk({
    clientID: "c3d7b9a2d0e430d3a85f",
    clientSecret: "3e14032a9dd87601757c4f2507bfccf7af04b9ee",
    repo: "johnphungtech",
    owner: "jphungcode",
    admin: ["jphungcode"],
    id: md5(slug), // Ensure uniqueness and length less than 50
    distractionFreeMode: true, // Facebook-like distraction free mode
  })

  useEffect(() => {
    gitalk.render("gitalk-container")
  }, [currentPostQuery])

  return (
    <>
      <Container>
        <PostContainer
          bgImage={featured_media.localFile.childImageSharp.fluid.src}
        >
          <PostMainContent toggle={menu} bgColor="#FFF">
            <PostHeader>
              <p>Reading time: {acf.readingDuration}</p>
              <p>{formatDate(date)}</p>
            </PostHeader>
            <Social socialConfig={{ title, slug, url, twitterHandle }} />
            <h1 dangerouslySetInnerHTML={{ __html: title }} />
            <div
              className="htmlContent"
              dangerouslySetInnerHTML={{ __html: content }}
            />
            <div id="gitalk-container" />
          </PostMainContent>
        </PostContainer>
        <Link to="/">
          <BackButton
            onClick={() =>
              categories[0].name == "Article"
                ? setArticle(true)
                : setProject(true)
            }
          >
            Back
          </BackButton>
        </Link>
      </Container>
    </>
  )
}

export default PostTemplate

export const pageQuery = graphql`
  query currentPostQuery($id: String!) {
    wordpressPost(id: { eq: $id }) {
      title
      content
      date
      slug
      acf {
        readingDuration
        gist_text
      }
      categories {
        name
      }
      featured_media {
        localFile {
          childImageSharp {
            fluid(maxWidth: 1000) {
              src
            }
          }
        }
      }
    }
    site {
      siteMetadata {
        title
        description
        author
        url
        twitterHandle
      }
    }
  }
`
